.tableContainer table {
    margin: 20px 0;
    width: 100%;
    border-collapse: collapse;
}

.tableContainer th{
    border-bottom: 1px solid var(--color-azul);
    padding: 8px;
    text-align: center;
    background-color: #f2f2f2;
}

.tableContainer tr:nth-child(even) {
    background-color: #f9f9f9;
}

.tableContainer tr:hover {
    background-color: #b7b3ffb1;
}

td {
    padding: 5px 10px;
    text-align: center;
}

.checkboxContainer input[type="checkbox"] {
    width: 16px;
    height: 16px;
}
.actionButton{
    background:none;
    border: none;
    cursor: pointer;
    font-size: 18px;
}

.actionButton:hover{
    color: var(--color-azul);
}